import { User } from "../state"

export default (state: User|null={
  username: "alex",
  active: true,
  id: 1,
  external_id: 1
}, action: any): User|null => {
  const {type, payload, error} = action
  switch(type){
    case "SET_USER":
    case "LOGIN_COMPLETED":
    case "LOAD_USER_COMPLETED":
      return error ? state : payload
    default:
      return state
  }
}